.main-nav {
  background-color: $textColor;
}

.main-nav__nav-head {
  display: flex;
  justify-content: space-between;
  padding: 20px 21px 16px 21px;
  border-bottom: 1px solid #000000;
}

.main-nav__logo {
  display: inline-block;

  svg {
    transition: 0.5s;

    &:nth-child(2),
    &:nth-child(3) {
      display: none;
    }

    &:hover,
    &:focus {
      fill: $linkColor;
      transition: 0.5s;
    }

    &:active {
      fill: rgba(255, 255, 255, 0.3);
    }
  }
}

.main-nav__button-menu {
  position: absolute;
  top: 0;
  right: 0;
  width: 75px;
  height: 55px;
  display: block;
  background-color: inherit;
  border: none;

  &:active {
    opacity: 0.3;
  }
}

.main-nav--open {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 3;
  background-color: $textColor;

  .main-nav__button-menu {
    cursor: pointer;

    &::before {
      content: "";
      position: absolute;
      width: 23px;
      height: 23px;
      top: 23px;
      right: 20px;
      background: url("../img/mobile/nav-close.svg") no-repeat;
      cursor: pointer;
    }
  }
}

.main-nav--close {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 3;
  background-color: rgba(0, 0, 0, 0.3);

  .main-nav__button-menu {
    cursor: pointer;

    &::before {
      content: "";
      position: absolute;
      width: 50px;
      height: 24px;
      top: 21px;
      right: 20px;
      background: url("../img/mobile/nav-open.svg") no-repeat;
      cursor: pointer;
    }
  }
}

.main-nav__nav-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}

.main-nav__nav-item {
  font-weight: 700;
  font-size: 18px;
  line-height: 32px;
  text-transform: uppercase;
  border-top: inset 1px rgba(255, 255, 255, 0.15);
  border-bottom: 1px solid #000000;

  a {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 63px;
    transition: 0.5s;

    &:hover,
    &:focus {
      color: $linkColor;
      transition: 0.5s;
    }

    &:active {
      color: rgba(210, 40, 86, 0.3);
    }
  }
}

@media (min-width: $tabletWidth) {
  .main-nav__logo {
    svg {
      &:nth-child(2) {
        display: inline-block;
      }

      &:nth-child(3),
      &:nth-child(1) {
        display: none;
      }
    }
  }

  .main-nav__button-menu {
    top: 22px;
  }

  .main-nav__nav-head {
    padding: 30px 20px;
  }
}

@media (max-width: 1199px) {
  .main-nav--close {
    .main-nav__nav-list {
      display: none;
    }
  }
}

@media (min-width: $desktopWidth) {
  .main-nav {
    position: static;
    width: 940px;
    display: flex;
    padding: 0 130px;
    margin: 0 auto;
    background-color: transparent;
  }

  .main-nav__nav-head {
    padding: 27px 0 36px 0;
    border: none;
  }

  .main-nav__logo {
    svg {
      &:nth-child(3) {
        display: inline-block;
      }

      &:nth-child(2),
      &:nth-child(1) {
        display: none;
      }
    }
  }

  .main-nav__button-menu {
    display: none;
  }

  .main-nav__nav-list {
    width: 620px;
    flex-direction: row;
    column-gap: 79px;
    flex-wrap: wrap;
    align-items: center;
    margin-left: auto;
  }

  .main-nav__nav-item {
    text-transform: none;
    font-weight: 400;
    line-height: 18px;
    border: none;

    a {
      height: 40px;

      &:active {
        color: rgba(255, 255, 255, 0.3);
      }
    }

    &--index-page {
      border-bottom: 2px solid #ffffff;
    }

    &--catalog-page {
      border-bottom: 2px solid #ffffff;
    }

    &--form-page {
      border-bottom: 2px solid #ffffff;
    }
  }
}
