.main-header {
  color: #ffffff;
  background-image: url("../img/mobile/bg-header.png");
  background-repeat: no-repeat;
  background-position: 50% 257px;
  background-color: $specColor;
  background-size: 660px 460px;

  a {
    text-decoration: none;
    color: #ffffff;
  }

  @media (min-resolution: 2dppx) {
    background-image: url("../img/mobile/bg-header@2x.png");
    background-repeat: no-repeat;
    background-size: 660px 460px;
    background-color: $specColor;
  }
}

.main-header--js {
  padding-top: 71px;
  background-position: 50% -2px;
}

.main-header__nav-contain {
  background-color: $textColor;
}

.main-header__head-text {
  width: 280px;
  font-weight: 300;
  font-size: 24px;
  line-height: 29px;
  margin: 0 auto;
  text-align: center;
}

.main-header__catalog-preview {
  width: 172px;
  font-size: 24px;
  line-height: 33px;
  font-weight: 300;
  text-align: center;
  margin: 0 auto;
  padding-top: 22px;
  padding-bottom: 78px;
}

.main-header__form-preview {
  font-size: 24px;
  line-height: 33px;
  font-weight: 300;
  text-align: center;
  margin: 0;
  padding: 23px 20px 44px 20px;
}

.main-header__head-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 280px;
  background: url("../img/mobile/hdr-phone.png") no-repeat;
  background-position: 0 100px;
  background-color: rgba(29, 38, 49, 0);
  padding: 20px 20px 38px 20px;
  margin: 0 auto;

  @media (min-resolution: 2dppx) {
    background: url("../img/mobile/hdr-phone@2x.png") no-repeat;
    background-size: 320px 286px;
    background-position: 0 100px;
  }
}

.main-header__platform-block {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 320px;
}

.main-header__button-download {
  font-weight: 700;
  font-size: 18px;
  line-height: 18px;

  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 280px;
  height: 49px;
  border: 3px solid #ffffff;
  border-radius: 29px;
  margin-top: 20px;
  padding: 0;
  transition: 0.5s;

  &:hover,
  &:focus {
    background-color: #ffffff;
    color: $linkColor;
    transition: 0.5s;
  }

  &:active {
    color: rgba(210, 40, 86, 0.3);
  }
}

.main-header__platform-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  margin: 32px 0 0 0;
  padding: 0;
  width: 204px;
  height: 56px;
}

.main-header__platform-item {
  a {
    display: inline-block;
    width: 50px;
    height: 52px;
  }

  &--ios {
    width: 47px;
    height: 56px;
    background: url("../img/apple-logo.svg") no-repeat;
  }

  &--android {
    width: 43px;
    height: 47px;
    background: url("../img/android-logo.svg") no-repeat;
  }

  &--windows-phone {
    width: 44px;
    height: 41px;
    background: url("../img/microsoft-logo.svg") no-repeat;
  }
}

.main-header__platform-info {
  font-weight: 300;
  text-align: center;
  margin-top: 22px;
  margin-bottom: 0;
}

@media (min-width: $tabletWidth) {
  .main-header {
    background-image: url("../img/tablet/bg-header.png");
    background-repeat: no-repeat;
    background-color: $specColor;
    background-size: 1200px 728px;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/tablet/bg-header@2x.png");
      background-repeat: no-repeat;
      background-color: $specColor;
      background-size: 1200px 728px;
    }
  }

  .main-header__head-text {
    font-size: 40px;
    line-height: 48px;
    width: 245px;
    margin: 58px auto 0 28px;
  }

  .main-header__catalog-preview {
    width: 358px;
    font-size: 50px;
    line-height: 68px;
    padding-top: 104px;
    padding-bottom: 129px;
  }

  .main-header__form-preview {
    font-size: 50px;
    line-height: 68px;
    padding-top: 105px;
    padding-bottom: 60px;
  }

  .main-header__head-info {
    width: 620px;
    height: 545px;
    background: url("../img/tablet/hdr-phone.png") no-repeat;
    background-position: 65% bottom;
    padding-top: 0;
    margin-top: 74px;

    @media (min-resolution: 2dppx) {
      background: url("../img/tablet/hdr-phone@2x.png") no-repeat;
      background-size: 660px 545px;
      background-position: 65% bottom;
    }
  }

  .main-header__platform-block {
    margin-top: 10px;
    margin-right: auto;
    padding-top: 0;
    padding-bottom: 0;
    background-color: inherit;
  }

  .main-header__button-download {
    width: 294px;
  }

  .main-header__platform-list {
    margin-top: 46px;
  }

  .main-header__platform-info {
    font-size: 18px;
    width: 300px;
    margin-top: 33px;
  }
}

@media (min-width: $desktopWidth) {
  .main-header {
    background-image: url("../img/desktop/bg-header.png");
    background-repeat: no-repeat;
    background-color: $specColor;
    background-size: cover;

    &--js {
      padding: 0;
    }

    @media (min-resolution: 2dppx) {
      background-image: url("../img/desktop/bg-header@2x.png");
      background-repeat: no-repeat;
      background-color: $specColor;
      background-size: cover;
    }
  }

  .main-header__nav-contain {
    background-color: rgba(0, 0, 0, 0.3);
  }

  .main-header__head-text {
    font-size: 50px;
    line-height: 68px;
    margin-top: 0;
    margin-left: 0;
    margin-right: 640px;
  }

  .main-header__form-preview {
    padding: 95px 130px 104px 130px;
  }

  .main-header__catalog-preview {
    padding-top: 58px;
    padding-bottom: 90px;
  }

  .main-header__head-info {
    height: 645px;
    background: url("../img/desktop/hdr-phone.png") no-repeat;
    background-position: 10% bottom;
    padding: 0 130px;
    margin-top: 98px;

    @media (min-resolution: 2dppx) {
      background: url("../img/desktop/hdr-phone@2x.png") no-repeat;
      background-size: 1050px 645px;
      background-position: 10% bottom;
    }
  }

  .main-header__platform-block {
    margin-top: 37px;
    margin-right: 640px;
  }

  .main-header__button-download {
    font-size: 21px;
    margin-top: 0;
  }

  .main-header__platform-list {
    margin-top: 70px;
  }

  .main-header__platform-info {
    margin-top: 50px;
  }
}
