.advantages {
  background: url("../img/white-triangle.svg") no-repeat;
  background-size: 100%;
  background-color: $bgColor;
}

.advantages__content {
  padding-top: 50px;
}

.advantages__image-block {
  display: flex;
  justify-content: center;
  align-items: center;
}

.advantages__image {
  display: block;

  &--grey {
    display: none;
  }

  &--pink {
    display: none;
  }

  &--hello {
    object-position: 50% 37px;
  }
}

.advantages__block-info {
  text-align: center;

  h3 {
    font-size: 18px;
    color: #000000;
    margin-bottom: 12px;
  }

  p {
    margin-top: 0;
    margin-bottom: 0;
  }

  &--mood {
    h3 {
      margin-top: 37px;
    }
  }

  &--world {
    h3 {
      margin-top: 35px;
    }
  }

  &--friends {
    margin-top: 31px;
  }
}

@media (min-width: $tabletWidth) {
  .advantages__content {
    display: grid;
    grid-template-columns: 268px 310px;
    column-gap: 42px;
    padding-top: 0;
  }

  .advantages__image-block {
    grid-column: 1;
    grid-row: 1;
  }

  .advantages__image {
    &--hello {
      width: 268px;
      height: 482px;
      object-fit: cover;
      object-position: 50% 0%;
    }
  }

  .advantages__zone-info {
    grid-column: 2;
    margin-top: 15px;
  }

  .advantages__block-info {
    width: 310px;
    text-align: left;

    &--mood {
      h3 {
        margin-top: 56px;
      }
    }

    &--world {
      p {
        width: 300px;
      }
    }
  }
}

@media (min-width: $desktopWidth) {
  .advantages__content {
    position: relative;
    display: flex;
    flex-direction: column;
    padding-top: 0;
    padding-bottom: 59px;
  }

  .advantages__image-block {
    order: 1;
    display: flex;
    column-gap: 137px;
  }

  .advantages__image {
    &--grey {
      display: block;
    }

    &--pink {
      display: block;
    }

    &--hello {
      width: 186px;
      height: 382px;
    }
  }

  .advantages__zone-info {
    width: 940px;
    position: absolute;
    top: 300px;
    order: 2;
    display: flex;
    column-gap: 20px;
    background-color: $bgColor;
    margin-top: 0;
    z-index: 5;
  }

  .advantages__block-info {
    position: relative;
    width: 252px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: inherit;

    &::before {
      content: "";
      position: absolute;
      top: -5px;
      width: 300px;
      height: 10px;
      background: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, #000000 49.8%, rgba(0, 0, 0, 0) 93.23%);
      opacity: 0.3;
      filter: blur(4px);
    }

    &--mood {
      margin-left: 23px;
    }

    &--world {
      margin-left: 50px;

      h3 {
        width: 220px;
      }
    }

    &--friends {
      margin-top: 0;
      margin-left: 50px;

      h3 {
        width: 230px;
      }
    }

    h3 {
      font-size: 30px;
      line-height: 30px;
      text-align: center;
      margin-top: 55px;
      margin-bottom: 24px;
    }

    p {
      font-size: 18px;
      line-height: 30px;
      width: 249px;
      text-align: center;
    }
  }
}
